:root {
  --font-family: "Sofia Pro", sans-serif;
  --content-width: 1440px;
  --container-offset: 40px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --dark-color: #131418;
  --body-color: #3A3A3A;
  --grey-color: #808080;
  --line-color: #E9E9E9;
  --accent-color: #ECCA7B;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable value-keyword-case */
@font-face {
  font-family: "Sofia Pro";
  src: url("../fonts/../fonts/SofiaProMedium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Sofia Pro";
  src: url("../fonts/../fonts/SofiaProRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Sofia Pro";
  src: url("../fonts/../fonts/SofiaProLight.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Sofia Pro";
  src: url("../fonts/../fonts/SofiaProUltraLight.woff2") format("woff2");
  font-weight: 200;
  font-display: swap;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}
.page__body.locked {
  overflow: hidden;
}

.content {
  display: flex;
  min-height: 100vh;
  position: relative;
}

.main {
  display: flex;
  width: calc(100% - max(275px, 19%));
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.font-styles {
  font-family: var(--font-family);
  font-weight: 400;
  font-style: normal;
  color: var(--dark-color);
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.sidebar {
  display: flex;
  justify-content: center;
  width: max(275px, 19%);
  padding-bottom: 5vh;
  border-right: 1px solid var(--line-color);
}
.sidebar__body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 220px;
}
.sidebar__adv {
  margin-top: 15vh;
  margin-bottom: 10vh;
}
.sidebar__link {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
}
.sidebar__link--line {
  width: 48px;
  height: 1px;
  background: var(--dark-color);
}
.sidebar__link--more {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.1;
  color: var(--dark-color);
  transition: color 0.2s ease-in;
}
.sidebar__link--more:hover {
  color: var(--accent-color);
}
.sidebar__link--more:active {
  opacity: 0.8;
}
.sidebar__link--more:focus {
  color: var(--accent-color);
}

.logo {
  display: flex;
  align-items: center;
  height: 14vh;
  z-index: 3;
}
.logo__img {
  width: 116px;
  height: 32px;
}

.user-panel {
  display: flex;
  align-items: center;
  height: 14vh;
  position: absolute;
  top: 0;
  right: 2.8vw;
  z-index: 3;
}
.user-panel__icon {
  width: 24px;
  height: 24px;
}
.user-panel__item {
  margin-left: 25px;
}
.user-panel__item:hover {
  opacity: 0.5;
  transition: opacity 0.2s ease-in;
}
.user-panel__item:active {
  transform: scale(0.85);
  transition: transform 0.2s ease-in;
}
.user-panel__item:focus {
  transform: scale(0.85);
}

.search {
  display: flex;
  align-items: center;
}
.search__btn:hover {
  opacity: 0.5;
  transition: opacity 0.2s ease-in;
}
.search__btn:active {
  transform: scale(0.85);
  transition: transform 0.2s ease-in;
}
.search__btn:focus {
  transform: scale(0.85);
}
.search__icon {
  width: 24px;
  height: 24px;
}
.search__input {
  outline: none;
  padding-left: 15px;
  display: none;
  font-family: var(--font-family);
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
}
.search__input::placeholder {
  font-family: var(--font-family);
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
}
.search__input._active {
  display: block;
}

.nav__list {
  display: flex;
  flex-direction: column;
}
.nav__item {
  padding: 3vh 0;
}
.nav__item:first-child {
  padding-top: 0;
}
.nav__item:last-child {
  padding-bottom: 0;
}
.nav__link {
  display: flex;
  align-items: center;
  font-size: 22px;
  line-height: 1.2;
  color: var(--body-color);
  transition: color 0.2s ease-in;
}
.nav__link:hover {
  color: var(--accent-color);
}
.nav__link:active {
  opacity: 0.8;
}
.nav__link:focus {
  color: var(--accent-color);
}
.nav__tag {
  margin-left: 12px;
}

.tag {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.1;
  color: var(--light-color);
  text-align: center;
  padding: 4px 8px;
  border-radius: 40px;
  background: rgba(0, 0, 0, 0.2);
}

.adv {
  display: flex;
  flex-wrap: wrap;
  column-gap: 6px;
  row-gap: 0;
  align-items: center;
}
.adv__bg {
  background: var(--dark-color);
  width: 110px;
  color: var(--light-color);
}
.adv__bg--text {
  font-family: var(--font-family);
  display: flex;
  height: 50px;
  font-weight: 100;
  font-size: 44px;
  line-height: 1;
}
.adv__bg .adv__bg--text {
  font-weight: 400;
}
.adv__more {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 30px;
}
.adv__line {
  width: 48px;
  height: 1px;
  background: var(--dark-color);
}
.adv__link {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.1;
  color: var(--dark-color);
  transition: color 0.2s ease-in;
}
.adv__link:hover {
  color: var(--accent-color);
}
.adv__link:active {
  opacity: 0.8;
}
.adv__link:focus {
  color: var(--accent-color);
}

.scroll {
  animation: scroll-down 1.5s infinite;
}
.scroll__link {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.1;
  color: var(--dark-color);
  margin-left: 5px;
}

@keyframes scroll-down {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
}
.slider {
  position: relative;
}
.slider::after, .slider::before {
  content: "";
  position: absolute;
  top: 0;
  height: 100%;
  border: 0 solid var(--line-color);
  border-left-width: 1px;
}
.slider::before {
  width: 20%;
  left: 20%;
  border-right-width: 1px;
}
.slider::after {
  left: 60%;
}
.slider__slide {
  display: flex;
  align-items: center;
  padding-left: max(40px, 3.5%);
  padding-bottom: 20vh;
  position: relative;
}
.slider__item {
  max-width: 525px;
}
.slider__title {
  font-weight: 300;
  font-size: 62px;
  line-height: 1.5;
  letter-spacing: -0.02em;
  margin: 0;
  padding: 15px 0;
}
.slider__title--symbol {
  font-size: 22px;
  line-height: 1.5;
  top: -1.5rem;
  left: -5px;
}
.slider__subtitle {
  font-size: 21px;
  line-height: 1.5;
  opacity: 0.4;
  margin: 0;
}
.slider__desc {
  font-size: 21px;
  line-height: 1.5;
  opacity: 0.4;
  margin: 0;
  margin-bottom: 50px;
}
.slider__cta {
  display: flex;
  gap: 24px;
}
.slider__pic {
  position: absolute;
  left: 50%;
  top: 60%;
  transform: translate(-50%, -50%);
  width: 86%;
  z-index: -1;
}
.slider__pic--img {
  width: 100%;
  height: 100%;
}
.slider__pag {
  font-size: 13px;
  line-height: 1;
  font-weight: 600;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  line-height: 1;
  background: var(--light-color);
  border: 1px solid var(--grey-color);
  border-radius: 8px;
  min-width: 185px;
  padding: 27px 30px;
  transition: font-size 0.2s ease-in;
}
.btn:hover {
  font-size: 1rem;
}
.btn:active {
  opacity: 0.8;
}
.btn:focus {
  font-size: 1rem;
}
.btn--dark {
  background-color: var(--dark-color);
  color: var(--light-color);
}
.btn__icon {
  width: 20px;
  height: 20px;
  margin-right: 12px;
}
.btn__icon--bag {
  fill: var(--light-color);
}
.btn__icon--play {
  fill: none;
  stroke: var(--dark-color);
}

.modal__wrapper {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  display: none;
}
.modal__wrapper .modal {
  padding: 25px;
  border-radius: 5px;
  width: 600px;
  height: auto;
}
.modal__wrapper .modal__body {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background: var(--light-color);
  opacity: 0.8;
  border-radius: 5px;
  border: 3px solid var(--dark-color);
  height: 380px;
}
.modal__wrapper .modal__iframe {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  padding: 10px;
}
.modal__wrapper .modal__close {
  cursor: pointer;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
}
.modal__wrapper .modal__close--icon {
  width: 16px;
  height: 16px;
  fill: var(--light-color);
}

.footer {
  display: flex;
  justify-content: space-between;
  width: 75vh;
  transform: rotate(-90deg) translateX(100%);
  transform-origin: right bottom;
  position: absolute;
  bottom: 5vh;
  right: 2.8vw;
  z-index: 3;
}
.footer__menu {
  display: flex;
}
.footer__item {
  padding: 0 16px;
}
.footer__link {
  font-weight: 600;
  transition: color 0.2s ease-in;
}
.footer__link:hover {
  color: var(--accent-color);
}
.footer__link:active {
  opacity: 0.8;
}
.footer__link:focus {
  color: var(--accent-color);
}